import Vue from 'vue';
import Router from 'vue-router';

import Index from '@/js/page/Index';
import Home from '@/js/page/Home';
import Air from '@/js/page/AfterAir';
import Bus from '@/js/page/Bus';
import Near from '@/js/page/NearPoint';
import Land from '@/js/page/Land';
import Manta from '@/js/page/MantaPoint';

Vue.use(Router);

const routes = [
    {
        path: '*',
        name: 'Index',
        component: Index,
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
    },
    {
        path: '/air',
        name: 'Air',
        component: Air,
    },
    {
        path: '/bus',
        name: 'Bus',
        component: Bus,
    },
    {
        path: '/near',
        name: 'Near',
        component: Near,
    },
    {
        path: '/land',
        name: 'Land',
        component: Land,
    },
    {
        path: '/manta',
        name: 'Manta',
        component: Manta,
    },
];
const router = new Router({
    routes,
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }
        return { x: 0, y: 0 };
    },
    mode: 'hash',
});

// 设置路由标题
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next();
});

export default router;
